.active {
    background-color: #6d819c;
}

.js-slideshow,
.js-thumbnail {
    margin: 10px auto;
    overflow: hidden;
}

.js-slideshow-list,
.js-thumbnail-list {
    position: relative;
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
}

.js-slideshow-item-img,
.js-thumbnail-item-img {
    width: 100%;
    display: block;
}

.js-slideshow-button,
.js-thumbnail-button {
    text-align: center;
}